.title {
 position: relative;
 text-align: center;
 font-family: 'Montserrat', sans-serif;
 letter-spacing: 2px;
 width: 100%;

 h5 {
 font-size: 22px;
 margin: 0;
 text-transform: uppercase;
 padding-bottom: 10px;

 text-align: center;
 font-weight: bold;
 letter-spacing: 2px;
 color: #000;
 padding-bottom: 10px;
 }

 h5:after {
 content: '';
 background: #000;
 display: block;
 height: 3px;
 width: 150px;
 margin: 10px auto;
 }
}

.section-content-container {
 min-height: 250px;
}

.bg-content {
 padding-top: 40px;
 padding-bottom: 20px;
}

.placeholder-officer {
 width: 250px;
 height: 350px;
}

.our-team-box img{
 max-height:350px;
 height: auto;
 width: auto;
}

.our-team-box{
 text-align: center;
 align-content: center;
 padding: 10px;
 font-family: 'Montserrat', sans-serif;
 height: auto;
 width: auto;
}

.our-team-box h4 {
 padding-top: 10px;
 font-size: 18px;

 text-align: center;
 letter-spacing: 1px;
 color: #000;
}

.our-team-box h4:after {
 content: '';
 background: #000;
 display: block;
 height: 3px;
 width: 150px;
 margin: 10px auto;
}

.our-team-box p {
 font-size: 14px;
 font-style: italic;
}
